.dropdown-field {
  .h3-text();
  // .visually-hidden();

  position: absolute;
  z-index: 200;
  width: 100%;
  top: @input-field-height;
  left: 0;
  // right: 0;

  // display: flex;
  display: none;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  padding-right: 7px;

  background-color: #fff;
  border: 1px solid #colors[dark-shade-50];
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;

  box-sizing: border-box;

  transition: height 0.3s ease-out;

  &_is-expanded {
    /* width: 100%;
    height: 165px;
    margin: 0;
    padding-left: 15px;
    padding-right: 7px;

    white-space: initial;

    clip-path: initial;
    clip: initial;
    overflow: hidden; */

    display: initial;
  }

  &__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    margin-bottom: 22px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__buttonpane {
    padding-top: 7px;
  }

  &__text {
    display: inline-block;
    vertical-align: middle;
  }

  &__counter {
    position: relative;
    z-index: 100;

    width: 92px;

    text-align: center;
    // background-color: red;
  }

  &__button {
    position: absolute;
    z-index: 200;
    top: 50%;
    transform: translateY(-50%);

    display: inline-block;
    vertical-align: middle;

    width: 30px;
    height: 30px;
    line-height: 30px;

    text-align: center;
    font-size: 18px;
    color: #colors[dark-shade-50];
    border: 1px solid #colors[dark-shade-50];
    border-radius: 50%;

    box-sizing: border-box;

    &_remove {
      left: 0;
    }

    &_add {
      right: 0;
    }

    &_is-disabled {
      color: #colors[dark-shade-5];
      // border: 1px solid #colors[dark-shade-5];
      border-color: #colors[dark-shade-5];
    }
  }

  &__quantity {
    display: inline-block;
    vertical-align: middle;

    padding: 0 10px;
  }
}
